.container {
  flex: 1;
  padding: 0em;
  background: rgba(0, 0, 0, 0.8);
  height: 100%;
}

.compact,
.padded {
  background-color: transparent !important;
  border-radius: 0 !important;
  height: 100%;
  position: relative;
  overflow: auto;
}

.compact {
  padding: 0 1.5em;
}

.padded {
  padding: 1.5em;
}

.light .compact,
.light .padded {
  background: rgba(0, 0, 0, 0.5) !important;
}



